/* eslint-disable react-hooks/exhaustive-deps */
import {
  faCheckCircle,
  faSearch,
  faTimesCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Form, InputGroup } from "@themesberg/react-bootstrap";
import React, { useContext, useEffect, useState } from "react";
import { securedAxiosInstance } from "../../axiosConfig";
import Loader from "../../components/Loader";
import { AuthContext } from "../../context/AuthContext";
import { NotificationContext } from "../../context/NotificationContext";
import styles from "../../css/Companydetails.module.css";
import { dateFormatter } from "../../utils/dateformatter";
import * as uploadService from "../../services/fileUpload";

const CompanyDetails = ({ match }) => {
  const { params } = match;
  const { state } = useContext(AuthContext);
  const [companyDetails, setCompanyDetails] = useState({});
  const [eligible, setEligible] = useState(true);
  const [resumes, setResumes] = useState([]);
  const [selectedResume, setSelectedResume] = useState("");
  const [conditions, setconditions] = useState();
  const [additionalQues, setAdditionalQues] = useState(null);
  const [additionalAns, setAdditionalAns] = useState([]);
  const [inputStatus, setInputStatus] = useState(true);
  console.log(additionalAns);

  const [submitStatus, setStatus] = useState({
    loading: false,
    success: false,
    error: false,
  });
  const [alreadyAppliedStatus, setAlreadyAppliedStatus] = useState(false);
  const [alreadyPlacedStatus, setAlreadyPlacedStatus] = useState(false);
  const { showNotification } = useContext(NotificationContext);
  const [filesToUpload, setFiles] = useState([]);
  const [uploadFile, setUploadFile] = useState(false);

  var pref_locs = [];

  const fetchResumes = async () => {
    try {
      setStatus((current) => ({ ...current, loading: true }));
      const res = await securedAxiosInstance.get(`/students/resumes`);
      setResumes(res.data.resumes);
      setSelectedResume(res.data.resumes[0].url);
      setStatus((current) => ({ ...current, loading: false }));
    } catch (error) {
      console.log(error);
    }
  };

  const fetchCompany = async () => {
    try {
      setStatus((current) => ({ ...current, loading: true }));
      const company = await securedAxiosInstance.get(
        `/company-events/${params.eventid}`
      );
      if (company.data["is_additional_ques"] === true) {
        setAdditionalQues(company.data["additional_ques"]);
        setAdditionalAns(company.data["additional_ques"]);
        setInputStatus(false);
      } else {
        setAdditionalQues(null);
        setAdditionalAns(null);
      }

      const degreeBranchesMap = {};
      company.data.branchesAllowed.forEach((item) => {
        const { branch, degree } = item;
        if (degree in degreeBranchesMap) {
          degreeBranchesMap[degree].push(branch);
        } else {
          degreeBranchesMap[degree] = [branch];
        }
      });

      company.data.branchesAllowed = degreeBranchesMap;
      setCompanyDetails(company.data);
      setStatus((current) => ({ ...current, loading: false }));
    } catch (error) {
      console.log(error);
    }
  };
  const checkEligibility = async () => {
    try {
      const status = await securedAxiosInstance.get(
        `/company-events/is_student_eligible/${params.eventid}?studentID=${state.userDetails.student}`
      );
      if (status.data.eligible.conditions.already_applied)
        setAlreadyAppliedStatus(true);
      if (status.data.eligible.conditions.already_placed)
        setAlreadyPlacedStatus(true);
      setconditions(status.data.eligible.conditions);
      setStatus((current) => ({ ...current, loading: false }));
      setEligible(status.data.eligible.status);
    } catch (error) {
      console.log(error);
    }
  };

  // Submitting pdf form of additional question
  async function conv_url(file, question) {
    if (!file) return;
    const formData = new FormData();

    try {
      formData.append(`${question}`, file);

      const response = await uploadService.fileUpload(file);

      if (response.status === 200) {
        additionalAns.map((el) => {
          if (el.type == "file") {
            el.expectedAns = response?.data[0]?.url;
          }
        });

        return true;
      } else {
        return false;
      }
    } catch (error) {
      return false;
    }
  }

  // to withdraw students application
  const withdrawForRole = async (e) => {
    e.preventDefault();

    const id = params.eventid;
    try {
      await securedAxiosInstance
        .post(`https://api.tnpnsut.in/registrations/${id}`, {
          student_id: state.userDetails.student,
        })
        .then((res) => {
          if (res.status == 200) {
            console.log(res);
            setAlreadyAppliedStatus(false);
            setStatus((current) => ({
              ...current,
              loading: false,
              success: true,
            }));
            showNotification(res?.data);
            window.location.reload();
          } else {
            showNotification(res?.data);
          }
        });
    } catch (error) {
      showNotification("Error withdrawing from role!");
      throw error;
    }
  };

  const applyForRole = async () => {
    setStatus((current) => ({ ...current, loading: true }));

    try {
      // const uploadedFileUrls = filesToUpload.map(({ file, question }) => file.name);
      if (filesToUpload && filesToUpload.length > 0) {
        const uploadedFileUrls = await Promise.all(
          filesToUpload.map(({ file, question }) => conv_url(file, question))
        );

        // Check if all files were uploaded successfully
        const allFilesUploaded = uploadedFileUrls.every(
          (status) => status === true
        );

        if (!allFilesUploaded) {
          showNotification("Failed to apply in the company Event");
          return;
        }
      }

      const response = await securedAxiosInstance.put(
        `/company-events/apply/${params.eventid}`,
        {
          studentID: state.userDetails.student,
          student_resume: selectedResume,
          Additional_answers: additionalAns,
        }
      );

      setAlreadyAppliedStatus(true);
      setStatus((current) => ({ ...current, loading: false, success: true }));
      showNotification("Successfully Applied!");
    } catch (error) {
      setStatus((current) => ({
        ...current,
        loading: false,
        success: false,
        error: true,
      }));
      showNotification("Failed to apply in the company Event");
      console.log(error);
    }
  };

  useEffect(() => {
    fetchCompany();
    checkEligibility();
    fetchResumes();
  }, []);

  if (submitStatus.loading) return <Loader />;

  const isRegistrationDatePassed =
    new Date(companyDetails.lastDate).getTime() < Date.now();

  return (
    <div className={styles.outerdiv}>
      <div className={styles.companybar}>
        <h6>
          {companyDetails.company ? companyDetails.company.Name : ""} |{" "}
          {companyDetails.jobtitle}
        </h6>
      </div>
      <div className={styles.wrapper}>
        <div className={styles.contentdiv}>
          <div className={styles.hero}></div>
          <div
            className={`${styles.companylogo} d-flex justify-content-center align-items-center`}
          >
            <img
              src={`https://tnp-company-logos.s3.ap-south-1.amazonaws.com/${companyDetails.company?.Name}.png`}
              alt="Keep going!! You'll reach there."
            ></img>
          </div>
          <div className={styles.companycontentwrapper}>
            <div style={{ padding: "30px 0px 0px" }}>
              <h3>{companyDetails.jobtitle}</h3>
              <h6>
                {companyDetails.company ? companyDetails.company.Name : ""}
              </h6>
              <div className={styles.typetag}>{companyDetails.type}</div>
              <hr style={{ marginTop: "30px" }} />
            </div>
            <div style={{ padding: "30px 0" }}>
              <div style={{ marginBottom: "30px" }}>
                <h5>Opening Overview</h5>
                <hr />
              </div>
              <div>
                <div className={styles.parent}>
                  <div className={styles.parenttag}>Place of Posting</div>
                  <div className={styles.parentcontent}>
                    {companyDetails.placeOfPosting}
                  </div>
                </div>
                <div className={styles.parent}>
                  <div className={styles.parenttag}>Batch</div>
                  <div className={styles.parentcontent}>
                    {companyDetails.batch}
                  </div>
                </div>
                <div className={styles.parent}>
                  <div className={styles.parenttag}>CTC/Stipend</div>
                  <div className={styles.parentcontent}>
                    {companyDetails.stipend}
                  </div>
                </div>
                <div className={styles.parent}>
                  <div className={styles.parenttag}>CTC/Stipend Info.</div>
                  <div className={styles.parentcontent}>
                    {companyDetails.stipend_info
                      ? companyDetails.stipend_info
                      : "Not Mentioned"}
                  </div>
                </div>
                {(companyDetails.type === "Placement" ||
                  companyDetails.type === "Internship") && (
                  <>
                    <div className={styles.parent}>
                      <div className={styles.parenttag}>CGPA</div>
                      <div className={styles.parentcontent}>
                        {companyDetails.cgpa}
                      </div>
                    </div>
                    <div className={styles.parent}>
                      <div className={styles.parenttag}>Category</div>
                      <div className={styles.parentcontent}>
                        {companyDetails.category?.toUpperCase()}
                      </div>
                    </div>
                    <div className={styles.parent}>
                      <div className={styles.parenttag}>Backlogs</div>
                      <div className={styles.parentcontent}>
                        {companyDetails.backlogs}
                      </div>
                    </div>
                    <div className={styles.parent}>
                      <div className={styles.parenttag}>Allowed Branches</div>
                      <div className={styles.parentcontent}>
                        {Object.entries(companyDetails.branchesAllowed).map(
                          ([degree, branches]) => (
                            <div key={degree}>
                              <span className="fw-bolder">{degree}</span>:{" "}
                              {branches.join(", ")}
                            </div>
                          )
                        )}
                      </div>
                    </div>
                    <div className={styles.parent}>
                      <div className={styles.parenttag}>
                        Registration last date
                      </div>
                      <div className={styles.parentcontent}>
                        {dateFormatter(companyDetails.lastDate).toDateString()}{" "}
                        -
                        {dateFormatter(
                          companyDetails.lastDate
                        ).toLocaleTimeString()}
                      </div>
                    </div>
                  </>
                )}
                <div className={styles.parent}>
                  <div className={styles.parenttag}>Coordinaters</div>
                  <div
                    className={styles.parentcontent}
                    style={{ whiteSpace: "pre-line" }}
                  >
                    {companyDetails?.apc}
                  </div>
                </div>
                {companyDetails?.additional_info !== "" && (
                  <div className={styles.parent}>
                    <div className={styles.parenttag}>Additional Info</div>
                    <div className={styles.parentcontent}>
                      {companyDetails?.additional_info}
                    </div>
                  </div>
                )}
                {companyDetails?.optional_redirect !== "" && (
                  <div className={styles.parent}>
                    <div className={styles.parenttag}>Form Link</div>
                    <div className={styles.parentcontent}>
                      <a
                        href={companyDetails?.optional_redirect || "#"}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "blue", textDecoration: "underline" }}
                      >
                        Click Here
                      </a>
                    </div>
                  </div>
                )}
              </div>
            </div>
            <div style={{ padding: "30px 0" }}>
              <div style={{ marginBottom: "30px" }}>
                <h5>Job Description</h5>
                <hr />
              </div>
              <div style={{ whiteSpace: "pre-line" }}>
                {companyDetails.description}
              </div>
            </div>
            <div style={{ padding: "30px 0" }}>
              <div style={{ marginBottom: "30px" }}>
                <h5>Attached Documents</h5>
                <hr />
              </div>
              <div>
                {companyDetails.attachments &&
                  companyDetails.attachments.map((sn, index) => (
                    <div
                      key={index}
                      className={`${styles.file} mb-2`}
                      style={{ cursor: "pointer" }}
                      onClick={() => window.open(sn.url)}
                    >
                      <div className={styles.fileicon}>
                        <img src="/pdf.png" width="60%" alt="pdf" />
                      </div>
                      <div className={styles.filetitle}>{sn.name}</div>
                      <div className={styles.downloadicon}>
                        <i
                          style={{ color: "#1456cf", fontSize: "20px" }}
                          className="bi bi-download"
                        ></i>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          </div>
        </div>
        <div className={styles.resumediv}>
          <div style={{ padding: "30px 0" }}>
            <div style={{ marginBottom: "30px" }}>
              <h6>Eligibility Criteria Evaluation Result</h6>
              <p style={{ fontSize: "13px" }}>
                Evaluated At: {new Date().toDateString()}
              </p>
              <hr />
            </div>
            <div className={styles.eligiblewrapper}>
              {alreadyAppliedStatus && (
                <div style={{ width: "80%" }}>
                  <p
                    style={{
                      fontSize: "16px",
                      // display: "flex",
                      fontWeight: "bold",
                      color: "red",
                    }}
                  >
                    You have already applied for this opportunity.
                  </p>
                </div>
              )}

              {alreadyPlacedStatus && (
                <div style={{ width: "80%" }}>
                  <p
                    style={{
                      fontSize: "16px",
                      display: "flex",
                      fontWeight: "bold",
                      color: "red",
                    }}
                  >
                    You cannot apply for this opportunity because you are
                    already placed.
                  </p>
                </div>
              )}
              {(!alreadyAppliedStatus || !alreadyPlacedStatus) && (
                <div style={{ width: "80%" }}>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.cgpa ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}
                    CGPA Criteria
                  </p>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.batch ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}{" "}
                    Batch Criteria{" "}
                  </p>
                  {companyDetails?.optional_criteria === true ? (
                    <p style={{ fontSize: "16px", display: "flex" }}>
                      {conditions && !conditions.percentage_10th ? (
                        <FontAwesomeIcon
                          style={{
                            fontSize: "23px",
                            marginRight: "10px",
                            color: "green",
                          }}
                          icon={faCheckCircle}
                        />
                      ) : (
                        <FontAwesomeIcon
                          style={{
                            fontSize: "23px",
                            marginRight: "10px",
                            color: "red",
                          }}
                          icon={faTimesCircle}
                        />
                      )}{" "}
                      10th Percentage{" "}
                    </p>
                  ) : (
                    <div></div>
                  )}
                  {companyDetails?.optional_criteria === true ? (
                    <p style={{ fontSize: "16px", display: "flex" }}>
                      {conditions && !conditions.percentage_12th ? (
                        <FontAwesomeIcon
                          style={{
                            fontSize: "23px",
                            marginRight: "10px",
                            color: "green",
                          }}
                          icon={faCheckCircle}
                        />
                      ) : (
                        <FontAwesomeIcon
                          style={{
                            fontSize: "23px",
                            marginRight: "10px",
                            color: "red",
                          }}
                          icon={faTimesCircle}
                        />
                      )}{" "}
                      12th Percentage{" "}
                    </p>
                  ) : (
                    <div></div>
                  )}
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.branch ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}{" "}
                    Branch Criteria{" "}
                  </p>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.degree ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}{" "}
                    Degree Criteria{" "}
                  </p>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.backlogs ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}
                    Backlogs Criteria
                  </p>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.ban ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}
                    Ban Criteria
                  </p>
                  <p style={{ fontSize: "16px", display: "flex" }}>
                    {conditions && !conditions.gender ? (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "green",
                        }}
                        icon={faCheckCircle}
                      />
                    ) : (
                      <FontAwesomeIcon
                        style={{
                          fontSize: "23px",
                          marginRight: "10px",
                          color: "red",
                        }}
                        icon={faTimesCircle}
                      />
                    )}
                    Gender Criteria
                  </p>
                </div>
              )}
            </div>
          </div>
          <Form>
            <Form.Group className="mb-3">
              {!alreadyAppliedStatus &&
                eligible &&
                additionalQues?.map((ls) => (
                  <>
                    <Form.Label style={{ color: "black" }}>
                      {ls["ques"]}
                    </Form.Label>
                    {ls["type"] === "multi" ? (
                      <>
                        {ls["options"].map((option, index) => (
                          <div key={index} className="mb-3">
                            <Form.Check
                              type="checkbox"
                              id={`multi-checkbox-${index}`}
                              label={option}
                              onChange={(e) => {
                                const l = e.target.id;
                                const target = l[l.length - 1];
                                const flag = e.target.checked;

                                additionalAns.forEach((val) => {
                                  if (val["ques"] === ls["ques"]) {
                                    if (flag)
                                      val["expectedAns"] +=
                                        val["options"][target] + ", ";
                                    // pref_locs.push(val["options"][target]);
                                    // pref_locs = pref_locs.filter(
                                    //   (element) =>
                                    //     element !== val["options"][target]
                                    // );
                                    else {
                                      val["expectedAns"] = val[
                                        "expectedAns"
                                      ].replace(
                                        val["options"][target] + ", ",
                                        ""
                                      );
                                    }
                                    // val["expectedAns"] = pref_locs;
                                  }
                                  const allFilled = additionalAns?.every(
                                    (question) => question.expectedAns !== ""
                                  );
                                  setInputStatus(allFilled);
                                });
                              }}
                            />
                          </div>
                        ))}
                      </>
                    ) : ls["type"] === "single" ? (
                      <>
                        {ls["options"].map((option, index) => (
                          <div key={index} className="mb-3">
                            <Form.Check
                              type="radio"
                              id={`single-radio-${index}`}
                              label={option}
                              name={ls["ques"]}
                              onChange={(e) => {
                                const l = e.target.id;
                                const value = l[l.length - 1];
                                additionalAns.forEach((val) => {
                                  if (val["ques"] === ls["ques"]) {
                                    val["expectedAns"] = val["options"][value];
                                  }
                                  const allFilled = additionalAns?.every(
                                    (question) =>
                                      question.expectedAns.trim() !== ""
                                  );
                                  setInputStatus(allFilled);
                                  // additionalAns.map((i) => {
                                  //   if (i.expectedAns === "") {
                                  //     return setInputStatus(false);
                                  //   }
                                  //   return setInputStatus(true);
                                  // });
                                });
                              }}
                            />
                          </div>
                        ))}
                      </>
                    ) : ls["type"] === "file" ? (
                      <Form.Control
                        type="file"
                        required
                        accept=".pdf"
                        onChange={(e) => {
                          const file = e.target.files[0];

                          if (file) {
                            const updatedFiles = filesToUpload.filter(
                              (f) => f.question !== ls["ques"]
                            );

                            updatedFiles.push({ file, question: ls["ques"] });

                            setFiles(updatedFiles);
                            additionalAns.forEach((val) => {
                              if (val["ques"] === ls["ques"]) {
                                val[
                                  "expectedAns"
                                ] = `https://tnpnsut-files.s3.ap-south-1.amazonaws.com/${e.target.files[0].name}`;
                              }
                              const allFilled = additionalAns?.every(
                                (question) => question.expectedAns.trim() !== ""
                              );
                              setInputStatus(allFilled);
                              // additionalAns.map((i) => {
                              //   if (i.expectedAns === "") {
                              //     return setInputStatus(false);
                              //   }
                              //   return setInputStatus(true);
                              // });
                            });
                          }
                        }}
                      />
                    ) : (
                      <Form.Control
                        type="text"
                        required
                        placeholder={ls["expectedAns"]}
                        onChange={(e) => {
                          additionalAns.forEach((val) => {
                            if (val["ques"] === ls["ques"]) {
                              val["expectedAns"] = e.target.value;
                            }
                            const allFilled = additionalAns?.every(
                              (question) => question.expectedAns.trim() !== ""
                            );
                            setInputStatus(allFilled);
                            // additionalAns.map((i) => {
                            //   if (i.expectedAns === "") {
                            //     return setInputStatus(false);
                            //   }
                            //   return setInputStatus(true);
                            // });
                          });
                        }}
                      />
                    )}
                  </>
                ))}
              <Form.Label style={{ color: "black" }}>
                Select a Resume
              </Form.Label>
              <InputGroup>
                <InputGroup.Text>
                  <FontAwesomeIcon icon={faSearch} />
                </InputGroup.Text>
                <Form.Select
                  value={selectedResume}
                  onChange={(e) => {
                    setSelectedResume(e.target.value);
                  }}
                >
                  {resumes &&
                    resumes.map((sn, index) => (
                      <option key={index} value={sn.url}>
                        {sn.name}
                      </option>
                    ))}
                </Form.Select>
              </InputGroup>
            </Form.Group>

            {alreadyAppliedStatus && (
              <Button
                type="submit"
                style={{
                  width: "100%",
                  background: "linear-gradient(to right, #2A2D3E, #202332)",
                  borderColor: "#202332",
                  marginTop: "1rem",
                }}
                disabled={isRegistrationDatePassed || !alreadyAppliedStatus}
                onClick={(e) => withdrawForRole(e)}
              >
                WithDraw
              </Button>
            )}

            {!alreadyAppliedStatus && (
              <Button
                type="submit"
                style={{
                  width: "100%",
                  background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                  borderColor: "#fb7b4a",
                  marginTop: "1rem",
                }}
                disabled={
                  !inputStatus ||
                  !eligible ||
                  selectedResume === "" ||
                  isRegistrationDatePassed ||
                  alreadyAppliedStatus
                }
                onClick={() => applyForRole()}
              >
                Apply
              </Button>
            )}
          </Form>
          {!isRegistrationDatePassed && !inputStatus && eligible && (
            <p
              style={{ color: "red", fontWeight: "bolder" }}
              className="text-red"
            >
              Please fill the above form to apply
            </p>
          )}
          {isRegistrationDatePassed && (
            <p style={{ color: "red" }} className="text-red">
              Registration Date has passed
            </p>
          )}
        </div>
      </div>
    </div>
  );
};
export default CompanyDetails;
